/* Style Definitions */
p.MsoNormal, li.MsoNormal, div.MsoNormal
{margin-top:0in;
margin-right:2.45pt;
margin-bottom:5.75pt;
margin-left:.5pt;
text-align:justify;
text-justify:inter-ideograph;
text-indent:-.5pt;
line-height:103%;
/* font-size:10.0pt; */
font-family:"Arial",serif;
color:black;}
h1
{mso-style-link:"Heading 1 Char";
margin-top:0.5in;
margin-right:0in;
margin-bottom:.05pt;
margin-left:.5pt;
text-indent:-.5pt;
line-height:107%;
page-break-after:avoid;
/* font-size:9.0pt; */
font-family:"Arial",sans-serif;
color:black;}
p.footnotedescription, li.footnotedescription, div.footnotedescription
{mso-style-name:"footnote description";
mso-style-link:"footnote description Char";
margin-top:0in;
margin-right:2.9pt;
margin-bottom:0in;
margin-left:1.5pt;
text-indent:-1.5pt;
line-height:99%;
font-size:9.0pt;
font-family:"Times New Roman",serif;
color:black;}
span.footnotedescriptionChar
{mso-style-name:"footnote description Char";
mso-style-link:"footnote description";
font-family:"Times New Roman",serif;
color:black;}
span.Heading1Char
{mso-style-name:"Heading 1 Char";
mso-style-link:"Heading 1";
font-family:"Arial",sans-serif;
color:black;
font-weight:bold;}
span.footnotemark
{mso-style-name:"footnote mark";
font-family:"Times New Roman",serif;
color:black;
vertical-align:super;}
.MsoChpDefault
{font-family:"Calibri",sans-serif;}
